.faq {
    display: flex;
    justify-content: space-between;
    position: relative;
    align-items: center;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 0;
    padding-bottom: -10rem;
    gap: 0;
  }
  
  .imageContainer {
    flex: 1;
    margin-right: 20px;
    border-radius: 10px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .imageContainer img {
    width: 100%;
    max-width: 400px;
    border-radius: 10px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .questionsContainer {
    flex: 2;
    display: flex;
    flex-direction: column;
  }
  
  .question {
    background-color: rgba(255, 255, 255, 0.9);
    height: 80px;
    width: auto;
    padding: 55px;
    margin-top: 10px;
    margin-bottom: 10px;
    border-radius: 10px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    position: relative;
    /* Agrega estas propiedades para centrar el texto */
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .answer {
    background-color: rgb(249, 249, 249);
    height: auto;
    width: auto;
    font-size: 16px;
    margin-left: 20px;
    margin-top: -30px;
    padding-bottom: 5px;
    border-radius: 0px 0px 10px 10px;
    line-height: 1.6;
    position: absolute;
  
    left: -20px;
    top: 100%;
    display: none;
    justify-content: center;
    align-items: center;
    z-index: 1;
  }
  
  .question.expanded .answer {
    display: block;
  }
  
  .questionsContainerdarkMode {
    flex: 2;
    display: flex;
    flex-direction: column;
  }
  
  .questiondarkMode {
    background-color: rgba(35, 35, 35, 0.331);
    height: 80px;
    width: auto;
    padding: 10px;
    margin-top: 10px;
    margin-bottom: 10px;
    border-radius: 10px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.906);
    cursor: pointer;
    position: relative;
    /* Agrega estas propiedades para centrar el texto */
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .answerdarkMode {
    background-color: rgb(18, 18, 18);
    height: auto;
    width: auto;
    font-size: 16px;
    margin-left: 20px;
    margin-top: -30px;
    padding-bottom: 5px;
    border-radius: 0px 0px 10px 10px;
    line-height: 1.6;
    position: absolute;
  
    left: -20px;
    top: 100%;
    display: none;
    justify-content: center;
    align-items: center;
    z-index: 1;
  }
  
  .questiondarkMode.expanded .answerdarkMode {
    display: block;
  }
  

  
